<template>
    <div class="fluid-container">
        <div class="panel entry-list">
            <DirectoryPanel v-bind:loading="loading" v-bind:items="items" v-bind:path="path"></DirectoryPanel>
        </div>

        <div class="panel markdown-body">
            <router-view v-bind:preview="preview" ></router-view>
        </div>


    </div>
</template>

<script>

    import GithubApi from '../github';
    import DirectoryPanel from "./DirectoryPanel";
    import { mapMutations, mapState } from 'vuex'

    export default {
        name: 'Entry',
        components: {
            DirectoryPanel
        },
        props: {
            "path": String
        },
        data() {
            return {
                loading: false,
                items: null,
                preview: null
            }
        },
        computed: {
            ...mapState([
                'dir',
                'file'
            ])
        },
        created() {
        },
        beforeDestroy() {
        },
        mounted() {
            this.refresh(this.path).then(() => {
                this.restoreHash();
            });
        },
        watch: {
            path(newValue, prevValue){
                if(this.$route.name === 'article'){
                    this.refresh(newValue);
                }
            }
        },
        methods: {
            ...mapMutations([
                'receiveFile',
                'receiveDir'
            ]),
            refresh(path){
                return this.showContent(path || '')
            },
            restoreHash(){
                if(!this.$route.hash){
                    return ;
                }

                let anchor = document.querySelector("[href='" + decodeURIComponent(this.$route.hash) + "']");
                if(anchor){
                    let headerOffset = 50;
                    let elementPosition = anchor.getBoundingClientRect().top;
                    let offsetPosition = elementPosition - headerOffset;

                    window.scrollTo({
                        top: offsetPosition,
                        behavior: "smooth"
                    });
                }
            },
            markedItem(path){
                this.receiveFile(this.items.find(it =>{
                    return it.path === path;
                }));
            },
            showContent(path) {
                let dir = null;
                this.loading = true;
                return GithubApi.contents(path).then((promise) => {
                    if(promise.type === 'file'){
                        this.preview = promise.data;
                        dir = GithubApi.parentDir(path);
                        //目录树需要更新
                        if(dir !== this.dir || this.items === null) {
                            return this.showContent(dir).then((dirPromise) => {
                                this.markedItem(path);
                                return dirPromise;
                            })
                        }
                        else {
                            this.markedItem(path);
                        }
                    }
                    else {
                        this.items = promise.data;
                        dir = path;
                    }
                    this.receiveDir(dir);
                    return promise;
                }).finally(() => {
                    this.loading = false;
                });
            }
        }
    }

</script>



