    @import '~github-markdown-css/github-markdown.css';

    .entry-list {
        flex: 1;
        margin-right: 1rem;
    }

    .markdown-body {
        flex: 3;
        box-sizing: border-box;
        padding: 1rem;
        overflow: hidden;
    }

    @media screen and (max-width: 767px) {
        .fluid-container {
            flex-direction: column-reverse;
        }

        .markdown-body {
            width: 100%;
        }

        .entry-list {
            width: 100%;
            margin-right: 0;
            margin-top: 1rem;
        }
    }



    .panel {
        background: #fff;
        border-radius: 2px;
    }


</style>
