<template>

    <div class="container" >

        <div class="editor-container" v-show="edit" >
            <ul class="mirrormark-toolbar">
                <li class="bold"><a v-on:click="takeAction('bold')"></a></li>
                <li class="italicize"><a v-on:click="takeAction('italicize')"></a></li>
                <li class="blockquote"><a v-on:click="takeAction('blockquote')"></a></li>
                <li class="code"><a v-on:click="takeAction('code')"></a></li>
                <li class="strikethrough"><a v-on:click="takeAction('strikethrough')"></a></li>
                <li class="link"><a v-on:click="takeAction('link')"></a></li>
                <li class="image"><a v-on:click="takeAction('image')"></a></li>
                <li class="unorderedList"><a v-on:click="takeAction('unorderedList')"></a></li>
                <li class="orderedList"><a v-on:click="takeAction('orderedList')"></a></li>
                <li class="preview"><a v-on:click="takeAction('preview')"></a></li>
            </ul>
            <codemirror :options="options" v-model="content" :events="['drop']" @drop="upload" ref="editor">

            </codemirror>
        </div>

        <div class="editor-preview markdown-body"  v-show="preview" v-html="markdown" >
        </div>

        <div class="fixed">
            <a href="#" v-on:click="changeMode('all')" v-bind:class="preview && edit && 'active' ">Edit & Preview</a>
            <a href="#" v-on:click="changeMode('edit')" v-bind:class="!preview && edit && 'active'">Edit Only</a>
            <a href="#" v-on:click="changeMode('preview')" v-bind:class="!edit && preview && 'active'">Preview Only</a>
        </div>

    </div>
</template>

<script>

    import {codemirror} from 'vue-codemirror'
    import 'codemirror/mode/markdown/markdown'
    import 'codemirror/mode/gfm/gfm'
    import 'codemirror/addon/fold/markdown-fold'
    import 'codemirror/addon/mode/overlay'


    import marked from 'marked'
    import Prism from 'prismjs';
    import 'prismjs/components';
    import 'prismjs/components/prism-autoit';

    import { mapState, mapMutations } from 'vuex'
    import GithubApi from '../github';

    marked.setOptions({
        renderer: new marked.Renderer(),
        highlight: function(code, lang, callback) {
            const language = Prism.languages[lang] || Prism.languages.autoit;
            return Prism.highlight(code, language);
        },
        gfm: true,
        breaks: false
    });

    export default {
        name: 'editor',
        components: {
            codemirror
        },
        computed: {
            ...mapState([
                "file"
            ])
        },
        props: {
            path: String
        },
        data() {
            return {
                editorHeight: 0,
                edit: true,
                preview: true,
                markdown: null,
                content: null,
                editor: null,
                options: {
                    tabSize: 4,
                    mode: 'text/x-gfm',
                    lineNumbers: true,
                    line: true,
                    lineWrapping: true,
                    theme: 'mirrormark',
                    viewportMargin: Infinity
                },
            }
        },
        watch: {
            content(curr, prev) {
                this.markdown = marked(curr);
                this.file.text = curr;
                this.receiveFile(this.file);
            }
        },
        mounted(){
            this.editor = this.$refs.editor.codemirror;
            if(this.path){
                GithubApi.contents(this.path, "raw").then((promise) => {
                    this.content = promise.data;
                    return promise;
                });
            }
        },
        methods: {
            ...mapMutations([
                'receiveFile'
            ]),
            changeMode(mode){
                if(mode && mode === 'edit'){
                    this.edit = true;
                    this.preview = false;
                }
                else if(mode && mode === 'preview'){
                    this.edit = false;
                    this.preview = true;
                }
                else{
                    this.edit = true;
                    this.preview = true;
                }
            },
            upload(editor, e){
                if(!/image\/.+$/.test(e.dataTransfer.files[0].type)){
                    return ;
                }
                this.text = "![" + e.dataTransfer.files[0].name + "]("+ e.dataTransfer.files[0].webkitRelativePath + ")";

                let coords = editor.coordsChar({
                    left: e.x,
                    top: e.y
                });

                editor.replaceRange(this.text, coords);

            },

            /**
             * Insert a string at cursor position
             * @param  {String} insertion
             */
            insert: function insert(insertion) {
                var doc = this.editor.getDoc();
                var cursor = doc.getCursor();

                doc.replaceRange(insertion, { line: cursor.line, ch: cursor.ch });
            },

            /**
             * Insert a string at the start and end of a selection
             * @param  {String} start
             * @param  {String} end
             */
            insertAround: function insertAround(start, end) {
                var doc = this.editor.getDoc();
                var cursor = doc.getCursor();

                if (doc.somethingSelected()) {
                    var selection = doc.getSelection();
                    doc.replaceSelection(start + selection + end);
                } else {
                    // If no selection then insert start and end args and set cursor position between the two.
                    doc.replaceRange(start + end, { line: cursor.line, ch: cursor.ch });
                    doc.setCursor({ line: cursor.line, ch: cursor.ch + start.length })
                }
            },

            /**
             * Insert a string before a selection
             * @param  {String} insertion
             */
            insertBefore: function insertBefore(insertion, cursorOffset) {
                var doc = this.editor.getDoc();
                var cursor = doc.getCursor();

                if (doc.somethingSelected()) {
                    var selections = doc.listSelections();
                    selections.forEach(function(selection) {
                        var pos = [selection.head.line, selection.anchor.line].sort();

                        for (var i = pos[0]; i <= pos[1]; i++) {
                            doc.replaceRange(insertion, { line: i, ch: 0 });
                        }

                        doc.setCursor({ line: pos[0], ch: cursorOffset || 0 });
                    });
                } else {
                    doc.replaceRange(insertion, { line: cursor.line, ch: 0 });
                    doc.setCursor({ line: cursor.line, ch: cursorOffset || 0 })
                }
            },

            /**
             * Default Actions
             * @type {Object}
             */
            takeAction: function(action) {
                switch (action) {
                    case 'bold':
                        this.insertAround('**', '**');
                        break;
                    case 'italicize':
                        this.insertAround('*', '*')
                        break;
                    case 'strikethrough':
                        this.insertAround('~~', '~~');
                        break;
                    case 'code':
                        this.insertAround('```\r\n', '\r\n```');
                        break;
                    case 'blockquote':
                        this.insertBefore('> ', 2);
                        break;
                    case 'orderedList':
                        this.insertBefore('1. ', 3);
                        break;
                    case 'unorderedList':
                        this.insertBefore('* ', 2);
                        break;
                    case 'image':
                        this.insertBefore('![](http://)', 2);
                        break;
                    case "link":
                        this.insertAround('[', '](http://)');
                        break;
                    case "hr":
                        this.insert('---');
                        break;
                    }
                }

        }

    }


</script>


<style scoped>

    @import '~codemirror/lib/codemirror.css';
    @import '~font-awesome/css/font-awesome.min.css';
    @import '~prismjs/themes/prism.css';
    @import '~prismjs/themes/prism.css';

    @import "./../assets/css/github.css";
    @import "./../assets/css/mirrormark.css";

    .container {
        position: fixed;
        top: 3rem;
        left: 0;
        right: 0;
        bottom: 0;
        min-width: 1125px;
        padding: 0 ;
        margin: 0 ;
        display: flex;
        background-color: #fff;
    }

    .editor-preview, .editor-container {
        flex: 1;
        max-width: 100%;
        height: 100%;
    }

    .editor-container{
        display: flex;
        flex-flow: column;
        overflow-y: hidden;
    }

    .editor-preview {
        border-left-width: 1px;
        border-left-style: solid;
        border-left-color: rgb(221, 221, 221);
        border-image: initial;
        padding: 20px;
        box-sizing: border-box;
        overflow-y: auto;
    }

    .vue-codemirror {
        flex: 1 1 auto;
        max-width: 100%;
        overflow-y: auto;
    }

    .fixed {
        z-index: 999;
        position: fixed;
        display: flex;
        flex-direction: column;
        top: 3rem;
        right: 0;
        opacity: 0;
    }

    .fixed:hover{
        opacity: 0.9;
        border-left: 1px solid #e0e0e0;
        border-bottom: 1px solid #e0e0e0;
    }

    .fixed a {
        flex: 1;
        display: block;
        background-color: #fff;
        padding: 10px 25px;
        cursor: pointer;
        color: #000;
        text-decoration: none;
        position: relative; /* 设置子元素为相对定位，可让子元素不继承Alpha值，保证字体颜色不透明 */
        border-top: 1px solid #fff;
        border-bottom: 1px solid #fff;
    }

    .fixed a.active {
        color: #ca0c16;
    }

    .fixed a:hover {
        -webkit-box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.05);
        box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.05);
    }
    .fixed a:hover:not(:last-child) {
        border-bottom: 1px solid #e0e0e0;
    }
    .fixed a:hover:not(:first-child) {
        border-top: 1px solid #e0e0e0;
    }

    /*控制整个滚动条*/
    ::-webkit-scrollbar {
        background-color: rgba(0,0,0,.05);
        width: 8px;
        height: 8px;
        background-clip: padding-box;
    }

    /*滚动条两端方向按钮*/
    ::-webkit-scrollbar-button {
        display: none;
    }

    /*滚动条中间滑动部分*/
    ::-webkit-scrollbar-thumb {
        background-color: #ccc;
        border-radius: 5px;
    }

    /*滚动条右下角区域*/
    ::-webkit-scrollbar-corner {
        background-color: #ccc;
    }


</style>
