<template>
    <div class="header">
        <div class="fluid-container">
            <div class="logo">MarkdownBook</div>
            <div class="nav">
                <ul>
                    <li><label><input type="search" placeholder="关键字" v-model="keyword" /><input type="button" value="搜索" v-on:click="search"/></label></li>
                    <template v-if="login === github.owner">
                        <template v-if="$route.name === 'editor'">
                            <li v-if="!file.sha"><label>
                                <input type="search" placeholder="文件名称" v-model="newPath" />
                                .md
                            </label></li>
                            <li><a v-bind:class="{ disabled: !!processing, blink: processing === 'save'}" href="javascript:void(0)" v-on:click="saveFile">保存</a> </li>
                            <li><a v-bind:class="{ disabled: !!processing }" href="javascript:void(0)" v-on:click="discardFile">返回</a> </li>
                        </template>

                        <template v-else>
                            <li><a v-bind:class="{ disabled: !!processing }" href="javascript:void(0)" v-on:click="newFile">新增</a> </li>

                            <template v-if="file.sha">
                                <li><a v-bind:class="{ disabled: !!processing }" href="javascript:void(0)" v-on:click="editFile">修改</a> </li>
                                <li><a v-bind:class="{ disabled: !!processing, blink: processing === 'remove'}" href="javascript:void(0)" v-on:click="removeFile">删除</a> </li>
                            </template>
                        </template>
                    </template>
                    <li>
                        <img v-if="login" class="avatar" v-bind:title="'@' + login + ' 登出'" v-bind:alt="'@' + login" v-bind:src="avatar" v-on:click="logout()">
                        <a v-else-if="!token" href="javascript:void(0)" @click.exact="goLogin(false)" @click.ctrl.exact="goLogin(true)">登录</a>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import GithubApi from '../github';
    import { mapMutations, mapState } from 'vuex'

    export default {
        name: "Header",
        computed: {
            ...mapState([
                'file',
                'dir'
            ])
        },
        data(){
            return {
                processing: false,
                login: null,
                avatar: null,
                keyword: null,
                newPath: null,
                token: null,
                github: GithubApi.config
            }
        },
        mounted(){
            this.initial();
        },
        watch: {
            "$route"(from, to){
                this.initial();
            },
        },
        methods: {
            ...mapMutations([
                "receiveFile"
            ]),

            initial(){
                //编辑文件状态
                if(this.$route.params.sha){
                    this.file.sha = this.$route.params.sha;
                    this.file.path = this.$route.params.path || '';
                }

                //新建文件状态
                if(this.$route.params.mkdir){
                    this.dir = this.$route.params.mkdir || "";
                }

                this.newPath = this.dir && (this.dir + "/newfile") || "newfile";

                let installToken = this.token;
                this.token = this.$route.query.access_token || sessionStorage.getItem("access_token");
                this.keyword = this.$route.params.keyword;

                //token change
                if(installToken !== this.token){
                    this.updateUser(this.token);
                }

            },

            search(){
                return this.$router.push({
                    name: "search",
                    params: {
                        keyword: this.keyword
                    }
                });
            },
            updateUser(token){
                if(token){
                    sessionStorage.setItem("access_token", token);
                    GithubApi.user().then(promise => {
                        this.login = promise.data.login;
                        this.avatar = promise.data.avatar_url + "&s=32";
                    })
                }
                else{
                    this.token = null;
                    this.login = null;
                    this.avatar = null;
                }
            },
            goHome(callback){
                return this.$router.push("/t/" + (this.file.path || this.dir));
            },
            goLogin(me){
                return this.$router.push({
                    name: "login",
                    query: {
                        me: me,
                        redirectUrl: window.location.href
                    }
                });
            },
            logout(){
                sessionStorage.removeItem("access_token"); //清除sessionStorage
                GithubApi.config.client = null; //清除全局变量
                this.updateUser(null);
            },
            newFile() {
                this.receiveFile({}); //清楚狀態
                return this.$router.push("/add/" + this.dir)
            },
            editFile() {
                return this.$router.push("/edit/" + this.file.sha + "/" + this.file.path)
            },
            removeFile() {
                if(this.processing){
                    return;
                }
                this.processing = "remove";
                GithubApi.remove(this.file).then(promise => {
                    this.receiveFile({});
                    return this.goHome();
                }).finally(() => {
                    this.processing = false;
                });
            },
            saveFile(){
                if(this.processing){
                    return;
                }
                this.processing = "save";
                if(!this.file.sha) {
                    this.file.path = this.newPath + '.md';
                }
                GithubApi.commit(this.file).then(promise => {
                    return this.goHome();
                }).finally(() => {
                    this.processing = false;
                });
            },
            discardFile(){
                this.goHome();
            }
        }
    }
</script>

<style scoped>
    .header {
        position: fixed;
        left: 0;
        top: 0;
        background: #fff;
        border-bottom: 1px solid #f1f1f1;
        color: #0366d6;
        height: 3rem;
        width: 100%;
        z-index: 100;
    }

    .nav {
        margin-top: -.5rem;
    }

    .nav ul {
        display: flex;
        justify-content: flex-end;
        align-items: start;
        line-height: 2rem;
    }

    .nav ul li {
        position: relative;
        cursor: pointer;
        padding: 0 1rem;
        font-size: 1rem;
        border-right: 1px solid hsla(0,0%,59.2%,.2);
    }

    .nav ul li a{
        color: #0366d6;
        text-decoration: none;
    }

    .logo {
        margin-right: 1rem;
        flex: 1;
        font-weight: bolder;
        font-size: 1rem;
    }

    .nav {
        flex: 3;
    }

    @keyframes blinker {
        50% { opacity: 0; }
    }

    .nav ul li a.disabled {
        pointer-events: none;
    }

    .nav ul li a.blink {
        animation: blinker 1s linear infinite;
    }

    .nav ul li input {
        line-height: 2rem;
        padding: 0 .5rem;
        border: 1px solid #e6edf4;
        border-radius: 2px;
        background-color: #f1f5fa;
        color: #0366d6;
    }

    .nav ul li input:focus {
        outline: none;
        border-color: #0366d6;
        background-color: #fff;
    }

    .nav .avatar{
        cursor: pointer;
        vertical-align:middle;
        height: 1rem;
        width: 1rem;
    }

</style>
