<template>
</template>

<script>
    import GithubApi from '../github';
    export default {
        name: "Login",
        mounted() {

            if(this.$route.query.me){
                let oauthApps = "client_id=225031944a6bf76a419c";
                window.location = "https://github.com/login/oauth/authorize?" + oauthApps + "&redirect_uri=" + encodeURIComponent(this.authorizeUrl(oauthApps)) + "&scope=repo&login=" + GithubApi.config.owner + "&state=" + Math.floor(Math.random()*100000);
            }
            else{
                let githubApps = "client_id=Iv1.fb87a658b1a64609";
                window.location = "https://github.com/login/oauth/authorize?" + githubApps + "&redirect_uri=" + encodeURIComponent(this.authorizeUrl(githubApps)) + "&state=" + Math.floor(Math.random()*100000);
            }
        },
        methods: {
            authorizeUrl(apps){
                return "http://nonwa11.xyz:8443/github.php?" + apps + "&redirect_uri=" + encodeURIComponent(this.$route.query.redirectUrl || "");
            }
        }
    }
</script>

