import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);


const state = {
    file: {},//当前文件信息
    dir: ""//当前目录
};

//setter
const mutations = {
    receiveFile(state, payload) {
        state.file = payload;
    },
    receiveDir(state, payload) {
        state.dir = payload;
    }
};

const actions = {
};

export default new Vuex.Store({
    state,
    mutations,
    actions
})
