import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue';
import Editor from './views/Editor.vue';
import Entry from './views/Entry.vue';
import SearchPanel from './views/SearchPanel.vue';
import ArticlePanel from './views/ArticlePanel.vue';
import Login from './views/Login.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          name: 'entry',
          path: '/',
          component: Entry,
          children: [
            {
              name: 'search',
              path: '/s/:keyword',
              component: SearchPanel
            },
            {
              name: 'article',
              path: '/t/:path*',
              component: ArticlePanel
            },
            {
              name: 'article',
              path: '/',
              component: ArticlePanel
            }
          ]
        },
        {
          path: '/edit/:sha/:path*',
          component: Editor,
          name: 'editor'
        },
        {
          path: '/add/:mkdir*',
          component: Editor,
          name: 'editor'
        },
        {
          name: 'login',
          path: '/login',
          component: Login
        }
      ]
    }
  ]
})
