<template>
    <ul class="entry-box">
        <li class="entry" v-on:click="parentDir()" v-bind:class="{ active: loadingParentDir(), disabled: dir === '' }">
            <div class="entry-body">
                <div class="entry-indicator" v-if="loadingParentDir() "><Loading/></div>
                {{dir && '返回上一层' || '根目录'}}
            </div>
            <div class="entry-footer">
                {{dir || "..."}}</div>
        </li>
        <li class="entry" v-for="it in items" v-on:click="processItem(it)" v-bind:class="{ active: it.path === path}">

            <div class="entry-body">
                <div class="entry-indicator" v-if="loading && it.path === path"><Loading/></div>
                {{it.name}}
            </div>
            <div class="entry-footer" v-if="it.type === 'file'">{{it.size}} byte</div>
            <div class="entry-footer" v-if="it.type === 'dir'">目录</div>
        </li>
    </ul>
</template>

<script>
    import Loading from './Loading.vue';
    import GithubApi from '../github';
    import {mapState} from 'vuex';

    export default {
        name: "DirectoryPanel",
        components: {
            Loading
        },
        props: {
            path: String,
            items: Array,
            loading: Boolean
        },
        computed: {
            ...mapState([
                "dir",
                "file"
            ])
        },
        methods: {
            redirect(path){
                //只有路由不同才跳转
                if(this.path !== path){
                    this.$router.push("/t/" + path);
                }
            },
            loadingParentDir() {
                return this.loading && GithubApi.parentDir(this.dir) === (this.path || '');
            },
            parentDir(){
                if (this.dir && !this.loading) {
                    this.redirect(GithubApi.parentDir(this.dir));
                }
            },
            processItem(it){
                if(!this.loading){
                    this.redirect(it.path);
                }
            }
        }
    }
</script>

<style scoped>
    .entry {
        position: relative;
        padding: 0.5rem 1rem;
    }

    .entry:not(:last-child) {
        border-bottom: 1px solid #f1f1f1;
    }

    .entry.active .entry-body {
        color: #7D2727;
    }

    .entry.disabled:hover {
        cursor: not-allowed;
        background-color: #fff;
    }

    .entry.disabled .entry-body {
        color: #bfbfbf;
    }

    .entry:hover {
        background-color: #f9f9f9;
        cursor: pointer;
    }

    .entry-indicator {
        position: absolute;
        right: 1rem;

    }

    .entry-body {
        font-size: 1rem;
        font-weight: bolder;
        color: #0366d6;
    }

    .entry-footer {
        font-size: 0.5rem;
        color: #b2bac2;
        text-align: right;
    }
</style>
